/* Reset list styles */
.nav-links {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-around; /* evenly distribute space around each link */
    background-color: #333; /* dark background for the navbar */
}

.nav-links li {
    display: block;
}

.nav-links a {
    display: block;
    padding: 15px 20px;
    text-decoration: none;
    color: white; /* white text color */
    font-weight: bold;
    font-family: Arial, sans-serif;
}

.nav-links a:hover {background-color: #555; /* slightly lighter background on hover */}
